<style>

    @media screen 
        and (min-width: 320px) 
        and (max-width: 767px) 
        and (orientation: portrait) {
        
        #CollectionFilter .select-input {
            max-width: 110px !important;
        }
        
        #CollectionFilter .switch-input {
            max-width: 110px !important;
            font-size: 90% !important;
        }

        #CollectionFilter .row {
            height: auto !important;
        }
    }

    #CollectionFilter #searchInputCol {
        margin-bottom: 15px !important;
    }

    #CollectionFilter .select-input {
        max-width: 160px;
    }

    #CollectionFilter .v-expansion-panel:before {
        box-shadow: none !important;
    }  
    
    #CollectionFilter .v-expansion-panel-content__wrap {
        padding-top: 24px;
    }   

    #CollectionFilter .v-input--selection-controls {
        margin-top: 8px !important;
    }
    
</style>

<template>
    <div id="CollectionFilter">
                    
        <v-card-title class="table-filters pb-0">

            <v-form style="width: 100%">
                <v-row 
                    class="mt-2 mb-1">
                    
                    <v-col 
                        id="searchInputCol"
                        cols="11" 
                        md="11" 
                        class="mt-0 mb-0 pt-0 pb-0">
                        <slot name="searchInput"></slot>

                    </v-col>

                    <v-col cols="1" md="1">                    
                        <v-icon 
                            @click="switchFilterShow"
                            color="primary"
                            large>
                            mdi-filter-{{showInner === undefined ? 'menu' : 'minus'}}
                        </v-icon>
                    </v-col>

                    <template>

                        <v-expansion-panels 
                            style="display: none"
                            ref="expPanels"
                            :value="showInner">
                            <v-expansion-panel>    
                         
                                <v-expansion-panel-content>
                                    <v-row v-for="(fltRow, index) in filterRows">
                                        <v-col
                                            v-for="flt in fltRow"
                                            :cols="flt.cols ? flt.cols : 'auto'" 
                                            :md="flt.md ? flt.md : 'auto'" 
                                            class="pt-0 mt-0 pt-0 pb-0">

                                            <v-btn
                                                v-if="flt.btn === true"
                                                style="margin-top: 12px !important"
                                                dense
                                                :outlined="flt.outlined"
                                                :block="flt.block"
                                                :label="flt.text"
                                                @click="flt.onClick($event)" />

                                            <v-checkbox
                                                v-if="!flt.btn && flt.check !== undefined"
                                                style="margin-top: 12px !important"
                                                class="switch-input"
                                                dense
                                                :label="flt.label"
                                                @change="updateFilterCheckbox(flt, $event)" 
                                                :true-value="flt.check"
                                                :false-value="false"
                                                :value="activeFiltersCheckbox[flt.prop]"/>

                                            <v-select
                                                class="select-input"
                                                dense
                                                v-if="!flt.btn && flt.check === undefined"
                                                :outlined="flt.outlined"
                                                :style="{ 
                                                    maxWidth: flt.block ? '100% !important' : '',
                                                    marginTop: flt.outlined ? '' : '10px'}"
                                                :prepend-icon="flt.icon && !flt.outlined ? `mdi-${flt.icon}` : ''"
                                                :prepend-inner-icon="flt.icon && flt.outlined ? `mdi-${flt.icon}` : ''"
                                                :label="flt.label"
                                                @input="updateFilterSelect(flt, $event)"
                                                :value="activeFiltersSelect[flt.prop]"
                                                :items="flt.options 
                                                        ? flt.options 
                                                        : getPossibleOptions(flt)" /> 
                                                                
                                        </v-col>
                                    </v-row>
                                </v-expansion-panel-content>
                            </v-expansion-panel>
                        </v-expansion-panels>



                        <br>
                        <br>

                    </template>

                </v-row>
            </v-form>

        </v-card-title>

    </div>
</template>

<script>
    
    export default {

        name: "CollectionFilter",

        data () {
            
            return {
                showInner: undefined,
                activeFiltersSelect: {},
                activeFiltersCheckbox: {}
            }
        },
        
        props: {
            
            value: Array,
            items: Array, // original collection
            filterRows: Array,
            show: {
                type: Boolean,
                default: false
            },
            icon: {
                type: String,
                default: 'filter'
            }

        /////////////////////////////
       //* ---> filterRows schema //
      /////////////////////////////

         /* 
            [ 
                [
                    {
                        prop: '',
                        label: '',
                        icon: '',
                        check: false,
                        options: []
                    }
                ]
            ]
         */

        },

        methods:  {

            updateFilterSelect (_flt, _val) {
                console.log(_val)
                this.activeFiltersSelect[_flt.prop] = _val;
                this.$emit('input', this.getFullFilteredItems())
            },

            updateFilterCheckbox (_flt, _val) {
                console.log(_val)
                this.activeFiltersCheckbox[_flt.prop] = _val;
                this.$emit('input', this.getFullFilteredItems())
            },

            getFullFilteredItems () {
                
                let _sf = this.getSelectFilteredItems();
                
                return _sf.filter((_item, _index) => {
                    
                    let _cbFilters = this.activeFiltersCheckbox

                    for (let $p in _cbFilters) {
                        
                        let _fval = _cbFilters[$p]
                        let _ival = _item[$p]

                        if (_fval === false) {
                            return true
                        } else {
                            
                            if (_ival === _fval) {
                                return true
                            }
                        }

                        return false
                    }
                })
            },
            
            getSelectFilteredItems () {
                return this.items.filter(_item => {                    
                    let _itvalSTR = Object.values(_item).toString();
                    return _itvalSTR.includes(this.getSelectFilteredValues())
                })
            },

            getSelectFilteredValues () {
                return Object.values(this.activeFiltersSelect).filter(_fv => {
                    return _fv !== 'Todos'
                })
            },

            getCheckboxFilteredItems () {

                return this.items.filter(_item => {                    
                    
                    let _cbFilters = this.activeFiltersCheckbox;
                    
                    for (let $p in _cbFilters) {
                         let _fval = _cbFilters[$p]
                        
                        if (!_fval) {
                            return _item
                        }

                        if (_fval && _item[$p] === _fval) {
                            return _item
                        }
                    }
                })
            },

            getPossibleOptions (_flt) {

                let _options = [],
                    _prop = _flt.prop;

                let options = this.items.map(_item => {
                    return _item[_prop]
                })

                _options = options;

                return ['Todos', ..._options]
            },

            switchFilterShow () {

                let $expPanels = this.$refs.expPanels.$el         
                
                if (this.showInner === undefined) {
                    this.showInner = 0
                    $expPanels.style.display = 'flex'
                    this.$emit('update:show', true)
                } else {
                    this.showInner = undefined
                    $expPanels.style.display = 'none'
                    this.$emit('update:show', false)
                }
            }
        },

        watch: {
            items (_newVal) {
                this.$emit('input', this.getFullFilteredItems())
            }
        },

        mounted () {

            this.filterRows.forEach((_fltRow, _index) => {
                _fltRow.forEach(_flt => {

                    if (_flt.btn) {
                        return
                    }
                    
                    if (_flt.check === undefined) {
                        this.activeFiltersSelect[_flt.prop] = 'Todos'
                    }

                    if (_flt.check !== undefined) {
                        this.activeFiltersCheckbox[_flt.prop] = false
                    }
                })
            })
        }
    }

</script>