import Vue from 'vue'
import VueRouter from 'vue-router'

import User from './components/pages/User.vue'
import Check from './components/pages/Check.vue'
import Infos from './components/pages/Infos.vue'
import Login from './components/pages/Login.vue'

Vue.use(VueRouter)

const rawRoutes = [

  {
    path: '/',
    name: 'Check',
    component: Check,
    icon: 'credit-card-check'
  },

  {
    path: '/infos',
    name: 'Infos',
    component: Infos,
    icon: 'credit-card-multiple'
  },

  {
    path: '/user',
    name: 'User',
    component: User,
    icon: 'account-check',
    nolist: true
  },

  {
    path: '/login',
    name: 'Login',
    component: Login,
    nolist: true
  }
]

const router = new VueRouter({
    routes: rawRoutes
})

router.beforeEach((to, from, next) => {

    const _sessionToken = window.localStorage.getItem("jwt")

    if (!_sessionToken && to.name !== 'Login') {
        next('/login')
    } else {
        next()
    }
})

export {
    rawRoutes
}

export default router