<style>
    
    #HumanVerifyInput div.code-input p.title {
        font-size: 90% !important;
        margin-bottom: 10px !important;
        font-style: italic;
        color: darkblue;
        opacity: 0.7;
        text-decoration: line-through;
        user-select: none !important;
    }

    #HumanVerifyInput div.code-input.code-input-passed input {
        border-color: green !important;
        color: green !important;
    }

    #HumanVerifyInput div.code-input.code-input-dark .title {
        color: white;
    }

    #HumanVerifyInput div.code-input.code-input-dark input {
        color: white;
    }

</style>

<template>
    <div id="HumanVerifyInput">

        <v-icon 
            dense
            class="mr-3"
            style="position: relative; bottom: 5px"
            :color="dark ? 'white' : ''">mdi-human</v-icon>

        <code-input 
            style="display: inline-block"
            :class=" `input code-input 
                ${passed ? 'code-input-passed' : '' }
                ${dark   ? 'code-input-dark'   : '' }` "
            :fieldHeight="30"
            :fieldWidth="45"
            :fields="4"
            :loading="loading" 
            :title="humanVerifyCode"
            v-on:change="humanVerifyDecoding = $event"
            v-on:complete="check()" />

    </div>
</template>

<script>

    import Chance from 'chance'
    import CodeInput from 'vue-verification-code-input'

    const allNumMaps = [

        { n: 1, t:     'um'},
        { n: 2, t:   'dois'},
        { n: 3, t:   'tres'},
        { n: 4, t: 'quatro'},
        { n: 5, t:  'cinco'},
        { n: 6, t:   'seis'},
        { n: 7, t:   'sete'},
        { n: 8, t:   'oito'},
        { n: 9, t:   'nove'}
    ]
    
    export default {
        
        name: 'HumanVerifyInput',

        props: {
            
            value: {
                type: Boolean,
                default: null
            },

            dark: {
                type: Boolean,
                default: false
            }
        },

        data () {
            return {
                loading: false,
                passed: false,
                humanVerifyCode: '',
                humanVerifyDecoded: '',
                humanVerifyDecoding: ''
            }
        },

        methods: {

            init () {

                let chance = new Chance(),
                    numMap = chance.pickset(allNumMaps, 4);

                const code = numMap.map(nm => nm.t).join(''),
                      decoded = numMap.map(nm => nm.n).join('')
                
                this.humanVerifyCode = code;
                this.humanVerifyDecoded = decoded;
            },

            check () {

                this.loading = true;
                
                let right = this.humanVerifyDecoded,
                    inputed = this.humanVerifyDecoding,
                    result = inputed === right

                setTimeout(() => {
                    this.loading = false;
                    this.passed = result;
                    this.$emit('input', result)
                }, 1500)
            }
        },

        created () {
            this.init()
        },

        components: {
            CodeInput
        }
    }

</script>