<script>

    import PageHeader from './components/PageHeader.vue'

    export default {

        name: 'App',

        data () {
            return {
                drawerActive: true,
                passDialogActive: false
            }
        },

        methods: {

            logout () {
                window.localStorage.removeItem("jwt")
                this.$router.push('login')
            },

            orientate (_route) {
                
                if (_route.meta.landscape) {
                    this.forceLandscape(true)
                } else {
                    this.forceLandscape(false)
                }

                setTimeout(() => {
                    window.scrollTo(0, 1)
                }, 1000)
            },

            fullscreen () {

                if (!document.fullscreenElement) {
                    document.documentElement.requestFullscreen()
                } else {
                    if (document.exitFullscreen) {
                        document.exitFullscreen()
                    }
                }
            },

            async requestRecharge () {

                console.log('Init requestRecharge...')

                let creditPacks = [
                    '100 <br> R$ 50,00',
                    '200 <br> R$ 75,00',
                    '300 <br> R$ 125,00', 
                    '500 <br> R$ 200,00',
                    '900 <br> R$ 300,00',
                    '1600 <br> R$ 475,00',
                    '3200 <br> R$ 800,00',
                    '6400 <br> R$ 1400,00',
                    '12800 <br> R$ 2000,00'
                ]

                let $packPrompt = await this.$swal({
                    title: 'Fazer recarga',
                    iconHtml: '<i class="mdi mdi-plus-circle-multiple"></i>',
                    input: 'radio',
                    inputOptions: creditPacks,
                    width: '70vw',
                    height: '70vh',
                    text: 'Escolha um pacote de créditos...',
                    showCancelButton: true,
                    confirmButtonText: 'Continuar',
                    cancelButtonText: 'Sair',
                    inputValidator: _val => {
                    if (!_val) {
                        return 'Escolha um pacote.'
                    }
                  }
                })

                if ($packPrompt.value) {

                    const sellers = [
                        '@Dutch +551154989146',
                        '@Dylan +556699715378'
                    ]

                    let $sellerPrompt = await this.$swal({
                        title: 'Fazer recarga',
                        iconHtml: '<i class="mdi mdi-check"></i>',
                        text: 'Escolha um representante:',
                        input: 'radio',
                        inputOptions: sellers,
                        width: '70vw',
                        height: '70vh',
                        showCancelButton: true,
                        confirmButtonText: 'Confirmar',
                        cancelButtonText: 'Sair',
                        inputValidator: _val => {
                        if (!_val) {
                            return 'Escolha um representante.'
                        }
                      }
                    })

                    if ($sellerPrompt.value) {

                        const _data = {
                            pack: creditPacks[$packPrompt.value],
                            seller: sellers[$sellerPrompt.value]
                        }

                        let wpNum = _data.seller.split('+')[1],
                            wpMes = _data.pack.replace('<br>', 'créditos');
                            wpMes = '*CHKCHG/REQ*: ' + wpMes   
                        
                        let wpURL = `https://api.whatsapp.com/send?phone=${wpNum}&text=${wpMes}`

                        window.open(wpURL, '_system')
                    }
                }


            },

            async updatePassword () {

                console.log('Init updatePassword...')

                let $passPrompt = await this.$swal({
                    title: 'Alterar senha',
                    iconHtml: '<i class="mdi mdi-key-variant"></i>',
                    input: 'text',
                    text: 'Digitie uma nova senha e confirme.',
                    showCancelButton: true,
                    confirmButtonText: 'Confirmar',
                    cancelButtonText: 'Sair',
                    inputValidator: _val => {
                    if (!_val || _val.length < 3) {
                        return 'Senha de conter o mínimo de 3 caractéres'
                    }
                  }
                })

                let data = {
                    newPassword: $passPrompt.value,
                    userID: this.myself._id
                }

                if ($passPrompt.isConfirmed) {

                    this.axios({
                        url: '/updatePassword',
                        method: 'post',
                        data
                    }).then(_res => {
                        this.notify('Senha alterada!', 'success', () => {
                            console.log('End updatePassword...')
                        })          
                    }).catch(_err => {
                        console.log('Fail at updatePassword()')
                        console.error(_err)
                    })
                }
            },

            setAxiosDefaults () {

                const _env = process.env.NODE_ENV,
                      _sessToken = window.localStorage.getItem('jwt');

                if (_sessToken) {
                    this.axios.defaults.headers.common['Authorization'] = `Bearer ${_sessToken}` 
                }

                if (_env === 'production') {
                    this.axios.defaults.baseURL = window.location.origin + '/services/'
                }

                if (_env === 'development') {
                    this.axios.defaults.baseURL = `http://${window.location.hostname}:3500/services/`
                }
            }
        },

        created () {
            
            this.setAxiosDefaults()
            
            this.fetchAndSetMyself({
                axios: this.axios, 
                route: this.$route
            })
        },

        components: {
            PageHeader
        }

    };

</script>