import Vue from 'vue'
import Vuex from 'vuex'
import VueJwtDecode from "vue-jwt-decode"
import { Plan } from './library/index.js'
import { deffSettings, paymentPlans } from './config.js'

Vue.use(Vuex)

let state = {
    myself: {}
}

let mutations = {
    
    setMyself (state, __user) {
        state.myself =  __user;
    }
}

let getters = {

    session: () => {

        const _sessToken = window.localStorage.getItem('jwt')

        if (_sessToken) {
            return VueJwtDecode.decode(_sessToken)
        }

        return {}
    },

    plan: () => {
        
        if (state.myself.name) {
            return new Plan(state.myself.currentPlan)
        }
        
        return new Plan(paymentPlans[0])
    },

    settings: (state) => {
        
        if (state.myself.name) {
            return state.myself.settings
        }
        
        return deffSettings
    }
}

let actions = {

    fetchAndSetMyself ({ commit }, $p) {

        const _notLoginPage = $p.route.name !== 'Login',
              _sessToken = window.localStorage.getItem('jwt');

        if (_notLoginPage && _sessToken) {

            $p.axios({
                url: '/getMyself',
                method: 'get'
            }).then(_res => {
                
                commit('setMyself', _res.data)
                console.log('state.myself set sucessfully')
            
            }).catch(_err => {
                console.log('Fail at fetchAndSetMyself()')
                console.error(_err)
            })
        }
    }
}

export default new Vuex.Store({
    state,
    mutations,
    actions,
    getters
})
