<script>

    export default {
        
        name: 'Infos',
        
        data () {
            return {
                showFilters: false,
                itemsPerPage: 20,
                /////////////
                activeDialog: {
                    menu: false,
                    status: false,
                    customer: false,
                    consultable: false,
                    info: { _cus: {} }

                },
                //////////////////////
                waitingBinCopy: false,
                /////////////////////
                loading: false,
                search: '',
                infosSelected: [],
                infosFiltered: [],
                allInfos: []
            }
        },

        methods: {

            fetchAndSetAllInfos () {

                this.loading = true;

                this.axios({
                    url: '/getAllInfos',
                    method: 'get'
                }).then(_r => {
                    this.allInfos = _r.data
                }).catch(_err => {
                    this.notify(_err.toString(), 'error')
                }).finally(() => {
                    this.loading = false;
                })
            },

            deleteListedInfo (_id) {
                
                this.loading = true;

                this.axios({
                    url: '/deleteInfo/' + _id,
                    method: 'post'
                }).then(_r => {
                    this.notify('Info deletada!', 'success', () => {
                        this.fetchAndSetAllInfos()
                    }, 500)
                }).catch(_err => {
                    this.notify(_err.toString(), 'error')
                    this.loading = false;
                })
            },

            deleteInfos (_infos) {
                this.loading = true;
                this.axios({
                    url: '/deleteManyInfos',
                    method: 'post',
                    data: _infos
                }).then(_r => {
                    this.notify('Infos deletadas!', 'success', () => {
                        this.infosSelected = []
                        this.fetchAndSetAllInfos()
                    }, 500)
                }).catch(_err => {
                    this.notify(_err.toString(), 'error')
                    this.loading = false;
                })
            },

            copyInfoMenu (_demo, _includeCus) {
                this.activeDialog.menu = false;
                setTimeout(() => {
                    if (_demo) this.infoToClipboard(this.activeDialog.info, true)
                    else this.infoToClipboard(this.activeDialog.info, false, _includeCus)
                }, 1000)
            },

            startBinCopy (_obj) {
                
                this.waitingBinCopy = true
                
                setTimeout(() => {

                    if (this.waitingBinCopy) {
                        this.infoToClipboard(_obj, true)
                    }

                    this.waitingBinCopy = false

                }, 1000)
            },

            checkParseUpdateLive (_info, _cb) {

                this.loading = true;

                this.axios({
                    url: '/checkParseLive',
                    method: 'post',
                    data: _info
                }).then(_r => {
                    
                    if (_r.data.error) {
                        this.notify(
                            'ESC: '
                            + _r.data.error
                            + '. Tentar novamente.',
                            'error', 
                        () => {
                            if (!_cb) this.fetchAndSetAllInfos()
                            if (_cb) _cb()
                        }, 1500); 
                        return;
                    }

                    this.notify('Live verificado!', 'info', () => {
                        this.editInfoAny(_r.data, () => {
                            this.notify('Info atualizada!', 'success', () => {
                                if (!_cb) this.fetchAndSetAllInfos()
                                    if (_cb) _cb()
                            }, 1000)
                        }, 1000)
                    })

                }).catch(_err => {
                    this.notify('Unknown error' + _err, 'error', () => {
                        if (!_cb) this.fetchAndSetAllInfos()
                        if (_cb) _cb()
                    }, 1500)
                })
            },

            checkParseUpdateLiveMany (_infos) {

                let $infos = [ ..._infos ],
                    $index = 0;

                const _main = () => {

                    this.notify('Carregando...', 'info', () => {
                        
                        let $currInfo = $infos[$index];

                        if ($currInfo) {
                            
                            if ($currInfo._live === 'Check' &&
                                    this.checkInfoBlocked($currInfo) === 'Não') {
                                
                                this.checkParseUpdateLive($currInfo, () => {
                                    $index = $index + 1;
                                    _main()
                                }, false)
                            
                            } else {
                                $index = $index + 1;
                                _main()
                            }

                        } else {
                            this.loading = false;
                            this.infosSelected = []
                            this.fetchAndSetAllInfos()
                        }
                    }, 3500)
                }

                _main()
            },

            editInfoAny (_info, _cb) {
                this.axios({
                    url: '/editInfo',
                    method: 'post',
                    data: _info
                }).then(_r => {
                    _cb(_r.data)
                }).catch(_err => {
                    this.notify(_err.toString(), 'error', () => {
                        this.loading = false
                    })
                })
            },

            editInfoStatus (_newStatus) {
                
                this.loading = true
                
                this.editInfoAny({
                    ...this.activeDialog.info,
                    _status: _newStatus
                }, () => {
                    this.notify('Info atualizada!', 'success', () => {
                        this.fetchAndSetAllInfos()
                        this.loading = false;
                        this.activeDialog.status = false;
                    })
                })
            },
            
            editInfoConsultable () {

                this.loading = true
                
                this.editInfoAny({
                    ...this.activeDialog.info,
                    _consultable: 'Sim'
                }, () => {
                    this.notify('Info atualizada!', 'success', () => {
                        this.fetchAndSetAllInfos()
                        this.loading = false;
                        this.activeDialog.consultable = false;
                    })
                })
            },

            editInfoCustomer () {

                this.notify('Função temporariamente inativa.', 'error')
                return;

                if (!this.$refs
                        .editInfoCustomerForm
                            .validate()) {
                    return;
                }

                this.loading = true
                
                this.editInfoAny({
                    ...this.activeDialog.info
                }, () => {
                    this.notify('Info atualizada!', 'success', () => {
                        this.fetchAndSetAllInfos()
                        this.loading = false;
                        this.activeDialog.customer = false;
                    }, 2000)
                })
            },

            genCustomer () {

                this.notify('Função temporariamente inativa.', 'error')
                return;

                console.log('...running genCustomer()')

                this.loading = true;

                this.axios({
                    url: '/getOneCustomer',
                    method: 'get'
                }).then(_r => {
                    this.notify('Dados prontos!', 'success', () => {
                        this.loading = false;
                        this.activeDialog.info._cus = _r.data;
                    })
                }).catch(_err => {
                    this.notify(_err.toString(), 'error', () => {
                        this.loading = false
                    })
                })
            },

            setupTableHeight () {

                const $win = window,
                      $header = $win.document.querySelector('header.v-sheet.v-toolbar'),
                      $colFlt = this.$refs.collectionFilter.$el,
                      $infTbl = this.$refs.infosTable.$el;

                const getTableHeight = () => {

                    let wH = $win.innerHeight,
                          hH = $header.offsetHeight,
                          fH = $colFlt.getBoundingClientRect().height,
                          tH = $infTbl.offsetHeight;

                    return wH - hH - fH + 'px'
                }

                setTimeout(() => {
                    $infTbl.style.height = getTableHeight()
                }, 500)
            }
        },

        watch: {
            
            'showFilters' () {
                this.setupTableHeight()
            },
            
            'windowHeight' () {
                this.setupTableHeight()
            },
            
            'windowWidth' () {
                this.setupTableHeight()
            }
        },

        mounted () {
            this.setupTableHeight()
            this.fetchAndSetAllInfos()
        }
    }

</script>
