<style>
    
</style>

<template>
    <div id="Login">

        <v-card
            :loading="loading" 
            class="mx-auto my-12 elevation-6"
            max-width="374">

            <v-form @submit.prevent="login" class="pa-5 pt-5 pb-0 mt-0">

                <v-row>
                    <v-col cols="12" md="6" class="pb-0">
                        <v-text-field
                            dense
                            autofocus
                            :rules="[ruls.required, ruls.minLength]"
                            v-model="loggingUser.name"
                            label="user" /> 
                    </v-col>
                    <v-col cols="12" md="6" class="pb-0">
                        <v-text-field
                            :rules="[ruls.required]"
                            v-model="loggingUser.password"
                            dense
                            label="pass" 
                            type="password" /> 
                    </v-col>
                    <v-col cols="9" md="8" class="pt-0 pb-0">
                        <human-verify-input v-model="humanVerified" />
                    </v-col>
                    <v-col 
                        cols="3" 
                        md="4" 
                        class="pt-0 pb-0" 
                        style="line-height: 500%">
                        <v-btn 
                            dense 
                            color="primary" 
                            type="submit">
                            <v-icon dense>mdi-login</v-icon>
                        </v-btn>
                    </v-col>
                </v-row>
                

            </v-form>
            
        </v-card>

    </div>
</template>

<script>

    export default {

        name: 'Login',

        data () {

            return {
                
                // general
                // loadings...
                loading: false,
                humanVerified: false,

                // login user data
                loggingUser: {
                    name: '',
                    password: ''
                }
            }
        },

        methods: {

            login () {

                if (!this.humanVerified) {
                    this.notify('Verificação incorreta.', 'error'); 
                    return;
                }

                this.loading = true;

                this.axios({
                    method: 'post',
                    url: 'sessionLoggingUser',
                    data: this.loggingUser
                }).then(_r => {

                    let _sessToken = _r.data.token;

                    console.log(_sessToken)

                    if (_sessToken) {
                      
                      window.localStorage.setItem("jwt", _sessToken)
                      
                      this.notify('Login feito com sucesso!', 'success', () => {
                            window.open('#/', "_blank")
                            window.close()
                      })
                    }

                }).catch(_err => {
                    this.notify('Erro de login: ' + _err, 'error', () => {
                        this.loading = false;
                    }, 3000)
                })
            }
        }
    }

</script>