import '@babel/polyfill'
import 'roboto-fontface/css/roboto/roboto-fontface.css'
import '@mdi/font/css/materialdesignicons.css'
import "vue-toastification/dist/index.css"
import 'sweetalert2/dist/sweetalert2.min.css'

import Vue from 'vue'
import App from './App.vue'
import router from './router.js'
import store from './store.js'
import vuetify from './plugins/vuetify'
import axios from 'axios'
import VueAxios from 'vue-axios'
import VueMoment from 'vue-moment'
import VueToast from 'vue-toastification'
import VueClipboard from 'vue-clipboard2'
import VueSweetalert2 from 'vue-sweetalert2'
import VueWindowSize from 'vue-window-size'
import HumanVerifyInput from './components/HumanVerifyInput.vue'
import CollectionFilter from './components/CollectionFilter.vue'
import CollectionAction from './components/CollectionAction.vue'
import FloatActionsButton from './components/FloatActionsButton.vue'
import library from './library/index.js'
import { rawRoutes } from './router.js'
import { mapGetters, 
         mapState,
         mapActions } from 'vuex'

Vue.config.productionTip = false

Vue.use(VueAxios, axios)
Vue.use(VueMoment)
Vue.use(VueWindowSize)
Vue.use(VueClipboard)
Vue.use(VueSweetalert2)

Vue.use(VueToast, {
    transition: "Vue-Toastification__bounce",
    maxToasts: 15,
    newestOnTop: false
})

Vue.mixin({

    data () {
        
        return {
            
            get win () {
                return window
            },

            get rawRoutes () {
                return rawRoutes
            },
            
            ruls: { // input rules
                required: value => !!value || 'Preencha o campo.',
                email: v => !v || /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(v) || 'E-mail inválido.',
                minLength: v => (!v || v.length > 3) ? true : 'Mínimo: 3 letras.',
                minLengthMedium: v => v.length > 5 || 'Mínimo: 5 letras.'
            }
        }
    },

    computed: {
        
        ...mapGetters([
            'plan',
            'session',
            'settings'
        ]),

        ...mapState([
            'myself'
        ])
    },

    methods: {

        ...mapActions([
            'fetchAndSetMyself',
        ]),

        ...library,

        notify (
            _text, 
            _type    = 'success',
            _cb      =  () => {},
            _timeout = 1500 ) {
              // error, warning, info, success
              this.$toast[_type](_text, {
                position: "bottom-right",
                timeout: _timeout,
                closeOnClick: false,
                pauseOnFocusLoss: false,
                pauseOnHover: false,
                draggable: true,
                draggablePercent: 1,
                showCloseButtonOnHover: false,
                hideProgressBar: false,
                closeButton: "button",
                icon: true,
                rtl: false,
                onClose: _cb || (() => {})
            })
        }
    },

    components: {
        HumanVerifyInput,
        CollectionFilter,
        CollectionAction,
        FloatActionsButton
    }
})

new Vue({
  router,
  store,
  vuetify,
  render: h => h(App)
}).$mount('#app')

window.infoTextToObj = library.infoTextToObj
window.infoObjToText = library.infoObjToText